import imageUpload from '@/components/image-upload.vue';
import request from '@/utils/request.js';

export default {
	components:{
		imageUpload
	},
	
	data() {
		return {
			limitNumber:9,
			enableDrag:true,
			imageData:[],
			title:'',
			content:''
		};
	},
	
	methods:{
		onInputTitle:function(e){
			setTimeout(() => { this.title = e.detail.value }, 0);
		},
		onInputContent:function(e){
			setTimeout(() => { this.content = e.detail.value }, 0);
		},
		sendMsg:async function(){
			if(this.imageData.length < 1){
				return uni.showToast({
					title:'请至少上传一张照片',
					icon:'none'
				})
			}
			uni.showLoading({ title: '处理中...' });
			const data ={
				title: this.title,
				content:this.content,
				images:this.imageData,
				createTime: Date.now()
			}
			const res = await request({
			  name: 'addShareMessage',
			  data,
			  needLogin: 1
			});
			uni.hideLoading();
			if(res.status === 0){
				uni.showModal({
				  content: `发送成功`,
				  showCancel: false,
					success:(modalRes)=>{
						if(modalRes.confirm) uni.navigateBack()
					}
				})
			} else {
				uni.showModal({
				  content: `添加数据失败，错误信息为：${res.msg}`,
				  showCancel: false
				})
			}
		}
	}
}
